@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;300;700&family=Ubuntu:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Josefin Sans",sans-serif}body{margin:0}main{margin:0 5% 0 5%;min-height:100%}.App-header{background-color:#302D3C;min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#F9F5F0}

*{font-family:"Josefin Sans",sans-serif}body{margin:0}main{margin:0 5% 0 5%;min-height:100%}.App-form{display:flex;flex-direction:column;background:#cdc9c3;padding:1em}.App-form ul{list-style-type:none;display:flex;justify-content:flex-start}.App-form ul li{margin:.5em}.App-form textarea{border-radius:.5em;height:7em;width:25em}.App-form form{display:flex;justify-content:flex-end;flex:1 1}.App-form form input{flex:1 1;border-radius:.5em;margin-right:.5em;margin-left:.5em;padding-left:1em;border:none}.App-form form button{background:#d9e4dd;border-radius:.5em;border:none}.App-form form button:checked{background:#444;color:#fff}.App-form p{min-height:20vh;background-color:#F9F5F0;border-radius:.5em}

.App-history{border:solid 2px #302D3C;margin:.5em;min-height:20vmin;border-radius:.3em}.App-history>h4{margin:.5em}

*{font-family:"Josefin Sans",sans-serif}body{margin:0}main{margin:0 5% 0 5%;min-height:100%}.App-results{border:solid 2px #4b5d67;margin:.5em;min-height:50vmin;border-radius:.3em}.App-results>h3{color:#302D3C;text-align:left;margin-left:.5em}.App-results>div{color:#302D3C;text-align:left;margin-left:.5em}#overlay{display:block;background:rgba(0,0,0,0.5);position:absolute;top:0;bottom:0;right:0;left:0}#overlay i{display:table-cell;vertical-align:middle;text-align:center}.spin-big{font-size:50px;height:50px;width:50px}.spin-normal{font-size:35px;height:35px;width:35px}.spin-small{font-size:20px;height:20px;width:20px}svg.tea{--secondary: #fff;position:relative;top:50%;left:50%}svg.tea #teabag{transform-origin:top center;transform:rotate(3deg);-webkit-animation:swing 2s infinite;animation:swing 2s infinite}svg.tea #steamL{stroke-dasharray:13;stroke-dashoffset:13;-webkit-animation:steamLarge 2s infinite;animation:steamLarge 2s infinite}svg.tea #steamR{stroke-dasharray:9;stroke-dashoffset:9;-webkit-animation:steamSmall 2s infinite;animation:steamSmall 2s infinite}@-webkit-keyframes swing{50%{transform:rotate(-3deg)}}@keyframes swing{50%{transform:rotate(-3deg)}}@-webkit-keyframes steamLarge{0%{stroke-dashoffset:13;opacity:0.6}100%{stroke-dashoffset:39;opacity:0}}@keyframes steamLarge{0%{stroke-dashoffset:13;opacity:0.6}100%{stroke-dashoffset:39;opacity:0}}@-webkit-keyframes steamSmall{10%{stroke-dashoffset:9;opacity:0.6}80%{stroke-dashoffset:27;opacity:0}100%{stroke-dashoffset:27;opacity:0}}@keyframes steamSmall{10%{stroke-dashoffset:9;opacity:0.6}80%{stroke-dashoffset:27;opacity:0}100%{stroke-dashoffset:27;opacity:0}}

