@import '../base.scss';

.App-results {
    border: solid 2px $dark-gray;
    margin: .5em;
    min-height: 50vmin;
    border-radius: .3em;
  }
  
  .App-results > h3 {
    color: #302D3C;
    text-align: left;
    margin-left: .5em;
  }
  
  .App-results > div {
    color: #302D3C;
    text-align: left;
    margin-left: .5em;
  }

  #overlay {
        /* width: 100%; */
    /* height: 100%; */
    display: block;
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}
#overlay i {
   display:table-cell;
    vertical-align:middle;
    text-align:center;
}
.spin-big {
    font-size: 50px;
    height: 50px;
    width: 50px;
 
}
.spin-normal {
    font-size: 35px;
    height: 35px;
    width: 35px;
}
.spin-small {
    font-size: 20px;
    height: 20px;
    width: 20px;
}

svg.tea {
    --secondary: #fff;
    position: relative;
    top: 50%;
    left: 50%;
  }
  svg.tea #teabag {
    transform-origin: top center;
    transform: rotate(3deg);
    animation: swing 2s infinite;
  }
  svg.tea #steamL {
    stroke-dasharray: 13;
    stroke-dashoffset: 13;
    animation: steamLarge 2s infinite;
  }
  svg.tea #steamR {
    stroke-dasharray: 9;
    stroke-dashoffset: 9;
    animation: steamSmall 2s infinite;
  }
  
  @keyframes swing {
    50% {
      transform: rotate(-3deg);
    }
  }
  @keyframes steamLarge {
    0% {
      stroke-dashoffset: 13;
      opacity: 0.6;
    }
    100% {
      stroke-dashoffset: 39;
      opacity: 0;
    }
  }
  @keyframes steamSmall {
    10% {
      stroke-dashoffset: 9;
      opacity: 0.6;
    }
    80% {
      stroke-dashoffset: 27;
      opacity: 0;
    }
    100% {
      stroke-dashoffset: 27;
      opacity: 0;
    }
  }
  